import React, { Component } from "react";
import Queue from "../models/Queue";
import Model from "../models/Model";
import {
  FlexibleWidthXYPlot,
  LabelSeries,
  HorizontalGridLines,
  VerticalGridLines,
  XAxis,
  YAxis,
  LineMarkSeries
} from "react-vis";
import "react-vis/dist/style.css";

class ModellingView extends Component {
  state = {};

  renderLines = lines => {
    return Object.keys(lines).map(lineId => {
      const line = lines[lineId];
      return (
        <LineMarkSeries
          data={line.values}
          key={lineId}
          color={line.color}
          strokeWidth={3.5}
          animation={"nowobble"}
          strokeDasharray={lineId.length > 3 ? [7, 3] : []}
        />
      );
    });
  };

  renderLabels = lines => {
    return Object.keys(lines).map(lineId => {
      const line = lines[lineId];
      const start = line.values[0];
      const end = line.values[1];
      const x = lineId.length > 3 ? start.x : start.x + (end.x - start.x) / 2;
      const y =
        lineId.length > 3
          ? start.y + Math.random() * 0.1 + 0.05
          : start.y + 0.1;
      return (
        <LabelSeries
          data={[{ x, y, label: lineId }]}
          style={{ fontSize: "8pt" }}
          key={lineId}
          labelAnchorY="auto"
          labelAnchorХ="auto"
        />
      );
    });
  };

  getMax = result =>
    Math.max(...[].concat(...Object.values(result)).map(o => o.values[1].x)) +
    0.5;

  render() {
    let { tasks, cs, queue: method } = this.props;
    if (typeof tasks === "function") {
      tasks = tasks();
      cs = cs();
    }
    const queue = new Queue(parseInt(method), tasks).run();
    const { result } = new Model(tasks, cs, queue).model();
    if (!result) return <div />;
    console.log(JSON.stringify(result));
    return (
      <React.Fragment>
        <h5 className="h5">Черга задач:</h5>
        <div>
          {Object.values(queue)
            .map(val => val.number)
            .join(", ")}
        </div>
        <FlexibleWidthXYPlot
          className="my-4"
          height={600}
          xType="linear"
          yType="linear"
          xDomain={[0.5, this.getMax(result)]}
          yDomain={[-0.5, Math.max(...cs.nodes.map(node => node.id)) + 0.5]}
        >
          <HorizontalGridLines />
          <VerticalGridLines />
          <XAxis title="Такт" position="end" />
          <YAxis
            title="ПЕ"
            position="end"
            tickValues={[0, ...cs.nodes.map(node => node.id)]}
            tickFormat={numb => parseFloat(numb).toFixed(0)}
          />
          {this.renderLines(result)}
          {this.renderLabels(result)}
        </FlexibleWidthXYPlot>
      </React.Fragment>
    );
  }
}

export default ModellingView;
