import paths from "./Paths";
import ExecutionError from "./Error";
import randomMC from "random-material-color";

const READY_STATUS = 1;
const RUNNING_STATUS = 2;
const FINISHED_STATUS = 3;
const TRANSMISSION_STATUS = 4;

class Model {
  constructor(tasks, cs, queue) {
    this.tasksData = tasks;
    this.csData = cs;
    this.queue = queue;
    this.tasks = this._formDependecies();
    this.processors = this._prepareProcessors();
    this.result = {};
    this.assignmentLog = {};
    this.tasksOnProcessors = {};
    this.colors = this._getColorArray();
    Object.keys(this.processors).forEach(
      processorId => (this.tasksOnProcessors[processorId] = [])
    );
  }

  _getColorArray = () => {
    const colors = {};
    const { nodes } = this.tasksData;
    nodes.forEach(node => (colors[node.id] = randomMC.getColor()));
    return colors;
  };

  _formDependecies = () => {
    const { nodes } = this.tasksData;
    const { edges } = this.tasksData;
    const dependencies = {};
    nodes.forEach(node => {
      dependencies[node.id] = { parents: {}, weight: node.weight };
    });
    edges.forEach(edge => {
      dependencies[edge.to].parents = {
        ...dependencies[edge.to].parents,
        [edge.from]: edge.weight
      };
    });
    Object.keys(dependencies).forEach(id => {
      if (Object.keys(dependencies[id].parents).length === 0)
        dependencies[id].status = READY_STATUS;
    });
    return dependencies;
  };

  _toEdgesArray() {
    const { edges } = this.csData;
    const graph = [];
    edges.forEach(edge => {
      graph.push([edge.from, edge.to]);
      graph.push([edge.to, edge.from]);
    });
    return graph;
  }

  _getPaths(from, to) {
    return paths({ graph: this._toEdgesArray(), from, to });
  }

  _prepareProcessors = () => {
    const { nodes: processors } = this.csData;
    const preparedProcessors = {};
    processors.forEach(processor => {
      preparedProcessors[processor.id] = {
        ...preparedProcessors[processor.id],
        status: READY_STATUS,
        standBy: 0
      };
    });
    return preparedProcessors;
  };

  _checkParent = (taskId, processorId, tact) => {
    const parents = Object.keys(this.tasks[taskId].parents).sort(
      (a, b) => this.assignmentLog[a].ended - this.assignmentLog[b].ended
    );
    const tacts = parents.map(parentId => {
      console.log(
        `Tasks on processors: ${JSON.stringify(this.tasksOnProcessors)}`
      );
      const parentAssignedInfo = this.assignmentLog[parentId];
      if (tact < parentAssignedInfo.ended) return tact;
      if (parentAssignedInfo.processorId !== processorId) {
        const parentProcessor = this.processors[parentAssignedInfo.processorId];
        if (parentProcessor.status === TRANSMISSION_STATUS)
          tact += parentProcessor.lastTransmittion;
        const intermediartry = this._getPaths(
          parseInt(parentAssignedInfo.processorId),
          parseInt(processorId)
        ).reduce((a, b) => (a.length < b.length ? a : b));
        console.log(
          `stops from ${
            parentAssignedInfo.processorId
          } to ${processorId}: ${intermediartry}`
        );
        const transmissionTacts = this.tasks[taskId].parents[parentId];
        const transmissionEndArray = [];
        intermediartry.forEach((stop, index) => {
          if (index === intermediartry.length - 1) return;
          const targetId = intermediartry[index + 1];
          const target = this.processors[targetId];
          const source = this.processors[stop];
          // console.log(`Processors: ${JSON.stringify(this.processors)}`);
          // if (this.tasksOnProcessors[stop].indexOf(parentId) !== -1)
          //   return tact;
          const transmissionStart =
            source.lastTransmittion &&
            source.lastTransmittion >= parentAssignedInfo.ended
              ? source.lastTransmittion
              : parentAssignedInfo.ended;
          if (transmissionStart < source.lastTransmittion)
            transmissionStart += source.lastTransmittion;
          const transmissionEnd = transmissionStart + transmissionTacts;
          source.status =
            tact >= transmissionEnd ? source.status : TRANSMISSION_STATUS;
          source.transmittingTask = parentId;
          this.tasks[parentId].status =
            tact >= transmissionEnd ? FINISHED_STATUS : TRANSMISSION_STATUS;
          source.endTransmissionOn = transmissionEnd;
          source.lastTransmittion = transmissionEnd;
          target.lastTransmittion = transmissionEnd;
          this.tasksOnProcessors[stop].push(parentId);
          this.tasksOnProcessors[targetId].push(parentId);
          console.log(
            `Sending ${parentId}, which is an ancester of ${taskId}, from ${stop} to ${targetId}. Starting tact: ${transmissionStart}, end tact: ${transmissionEnd}, ${JSON.stringify(
              parentAssignedInfo
            )}`
          );
          this.result = {
            ...this.result,
            [`${stop}->${targetId} (${parentId}->${taskId})`]: {
              values: [
                {
                  x: transmissionStart,
                  y: parseFloat(stop) + 0.25
                },
                {
                  x: transmissionEnd,
                  y: parseFloat(stop) + 0.25
                }
              ],
              color: this.colors[parentId]
            }
          };
          transmissionEndArray.push(transmissionEnd);
        });
        const max = Math.max(...transmissionEndArray);
        console.log(`Returning ${max}`);
        return max;
      }
      return tact;
    });
    console.log(`Tacts: ${JSON.stringify(tacts)}, max: ${Math.max(...tacts)}`);
    return !isFinite(Math.max(...tacts)) ? tact : Math.max(...tacts);
  };

  _executeTask = (taskId, processorId, tact) => {
    console.log(`Processors: ${JSON.stringify(this.processors)}`);
    tact = this._checkParent(taskId, processorId, tact);
    console.log(`---> Executting task ${taskId} on processor ${processorId}`);
    this.assignmentLog[taskId] = {
      processorId,
      ended: tact + this.tasks[taskId].weight
    };
    this.processors[processorId].status =
      this.processors[processorId].status === TRANSMISSION_STATUS
        ? TRANSMISSION_STATUS
        : RUNNING_STATUS;
    this.processors[processorId].from = tact;
    this.processors[processorId].to = tact + this.tasks[taskId].weight;
    this.processors[processorId].task = taskId;
    this.tasks[taskId].status = RUNNING_STATUS;
    this.queue = this.queue.filter(({ number: id }) => id !== taskId);
    this.result[taskId] = this.result[taskId]
      ? [...this.result[taskId]]
      : {
          values: [
            { x: tact, y: parseInt(processorId) },
            {
              x: tact + this.tasks[taskId].weight,
              y: parseInt(processorId)
            }
          ],
          color: this.colors[taskId]
        };
  };

  _getFreeProcessors = tackt => {
    const freeProcessorsIds = Object.keys(this.processors).filter(
      processorId => {
        const processor = this.processors[processorId];
        return (
          processor.status === READY_STATUS ||
          (processor.status === TRANSMISSION_STATUS && processor.to <= tackt)
        );
      }
    );
    if (freeProcessorsIds.length === 0) return -1;
    // console.log(`Free processors: ${JSON.stringify(freeProcessorsIds)}`);
    const processorsQueue = freeProcessorsIds
      .map(id => ({
        standBy: this.processors[id].standBy,
        id
      }))
      .sort((a, b) => b.standBy - a.standBy)
      .map(item => item.id);
    // let maxStandBy = -1,
    //   selectedId = 0;
    // Object.values(freeProcessorsIds).forEach(processorId => {
    //   const processor = this.processors[processorId];
    //   if (processor.standBy > maxStandBy) {
    //     maxStandBy = processor.standBy;
    //     selectedId = processorId;
    //   }
    // });
    // console.log(`Selected processorId: ${selectedId}`);
    // return selectedId;
    console.log(`Prioritised queue of processors: ${processorsQueue}`);
    return processorsQueue;
  };

  _getReadyTasks = () => {
    let readyTasks = this.queue.filter(({ number: taskId }) => {
      const task = this.tasks[taskId];
      if (Object.keys(task.parents).length === 0)
        return task.status === READY_STATUS;
      else {
        const preparedParents = [];
        Object.keys(task.parents).forEach(parentId => {
          preparedParents.push(
            this.tasks[parentId].status === FINISHED_STATUS ||
              this.tasks[parentId].status === TRANSMISSION_STATUS
          );
        });
        const isTaskReady = preparedParents.every(parent => parent);
        return isTaskReady;
      }
    });
    readyTasks = readyTasks.map(task => task.number);
    return readyTasks;
  };

  _checkFinish = tact => {
    Object.keys(this.processors).forEach(processorId => {
      const processor = this.processors[processorId];
      if (
        processor.status === RUNNING_STATUS ||
        processor.status === TRANSMISSION_STATUS
      ) {
        if (processor.to === tact) {
          console.log(`Task ${processor.task} finished on ${processorId}`);
          processor.status =
            processor.status === TRANSMISSION_STATUS
              ? TRANSMISSION_STATUS
              : READY_STATUS;
          processor.standBy = 0;
          this.tasks[processor.task].status = FINISHED_STATUS;
        }
      }
    });
  };

  _processorsStandBy = () => {
    Object.keys(this.processors).forEach(processorId => {
      const processor = this.processors[processorId];
      if (
        processor.status === READY_STATUS ||
        processor.status === TRANSMISSION_STATUS
      )
        this.processors[processorId].standBy++;
    });
  };

  _checkTransmittionFinish = tact => {
    Object.keys(this.processors).forEach(processorId => {
      const processor = this.processors[processorId];
      if (processor.status === TRANSMISSION_STATUS) {
        if (processor.endTransmissionOn <= tact) {
          processor.status =
            processor.to >= tact ? RUNNING_STATUS : READY_STATUS;
          processor.lastTransmittion = tact;
          this.tasks[processor.transmittingTask].status = FINISHED_STATUS;
          processor.transmittingTask = "-1";
          console.log(`Ending transmittion on processor ${processorId}...`);
        }
      }
    });
  };

  _canExecuteOnProcessor = (task, processorId, tact) => {
    // if (task === 5 && tact === 5) debugger;
    if (Object.keys(this.tasks[task].parents).length > 0) {
      const result = Object.keys(this.tasks[task].parents).map(parent => {
        const { processorId: parentProcessorId, ended } = this.assignmentLog[
          parent
        ];
        if (parentProcessorId !== processorId) {
          return this.tasks[parent].status !== TRANSMISSION_STATUS;
        }
        if (parentProcessorId === processorId) {
          return this.processors[parentProcessorId].transmittingTask !== parent;
        }

        return true;
      });
      return result.every(result => result);
    }
    return true;
  };

  model = () => {
    let tact = 0;
    // while (this.queue.length !== 0) {
    while (tact < 50) {
      tact++;
      console.log(`Tact: ${tact}`);
      console.log(`Processors: ${JSON.stringify(this.processors)}`);
      console.log(`Tasks: ${JSON.stringify(this.tasks)}`);
      console.log(`Queue: ${JSON.stringify(this.queue)}`);
      this._checkFinish(tact);
      this._checkTransmittionFinish(tact);
      const tasks = this._getReadyTasks();
      console.log(`Ready tasks: ${JSON.stringify(tasks)}`);
      try {
        if (tasks.length === 0) throw new ExecutionError(`No tasks available!`);
        for (let task of tasks) {
          const freeProcessors = this._getFreeProcessors(tact);
          if (freeProcessors.length <= 0)
            throw new ExecutionError(`No free processors`);
          for (let processor of freeProcessors) {
            console.log(
              `Trying task #${task} on ${processor}, canExecuteOnProcessor returned: ${this._canExecuteOnProcessor(
                task,
                processor
              )}`
            );
            if (this._canExecuteOnProcessor(task, processor, tact)) {
              this._executeTask(task, processor, tact);
              break;
            }
          }
        }
      } catch (e) {
        if (e instanceof ExecutionError || e instanceof TypeError)
          console.log(e.message);
        else throw e;
      } finally {
        this._processorsStandBy();
      }
    }
    return { result: this.result, tacts: tact };
  };
}

export default Model;
