import React, { Component } from "react";
import GraphView from "../components/GraphView";

class ModellingForm extends Component {
  state = {};

  constructor() {
    super();
    this.csGraphRef = React.createRef();
    this.tasksGraphRef = React.createRef();
  }

  renderGraphList(type) {
    const graphs = this.props.getGraphs(type);
    if (!graphs) return <option disabled>Відсутні графи</option>;
    return graphs.map(graph => (
      <option key={graph.id} value={graph.id}>
        {graph.name}
      </option>
    ));
  }

  render() {
    return (
      <form onSubmit={this.handleSubmit} className="p-2">
        <div className="form-group">
          <label className="h6" htmlFor="queue">
            Алгоритм формування черги задач:
          </label>
          <select
            name="queue"
            id="queue"
            value={this.props.queue}
            className="form-control"
            onChange={this.props.handleChange}
          >
            <option value="-1" disabled>
              Оберіть чергу
            </option>
            <option value="1">
              1 - У порядку спадання пронормованої суми критичних по часу і по
              кількості вершин шляхів до кінця графа задачі.
            </option>
            <option value="11">
              11 - У порядку спадання зв’язності вершин, а при рівних значеннях
              – в порядку зростання критичного по кількості вершин шляхів від
              початку графа задачі.
            </option>
          </select>
        </div>
        <div className="form-group">
          <label className="h6" htmlFor="assignmentAlgo">
            Алгоритм призначення:
          </label>
          <select
            name="assignmentAlgo"
            id="assignmentAlgo"
            value={this.props.assignmentAlgo}
            className="form-control"
            onChange={this.props.handleChange}
          >
            <option value="-1" disabled>
              Оберіть алгоритм
            </option>
            <option value="2">
              2 - Призначення на перший процесор,що звільнився. У цьому випадку,
              при призначенні, з вільних процесорів вибирається той, який
              звільнився і простоює довше за інших.
            </option>
            <option value="6" disabled>
              6 - Алгоритм «сусіднього» призначення з використанням моделювання
              для визначення початкового часу виконання обчислювальних робіт.
            </option>
          </select>
        </div>
        <div className="form-row">
          <div className="col">
            <label className="h6" htmlFor="tasks">
              Граф задач:
            </label>
            <select
              name="tasks"
              id="tasks"
              value={this.props.tasks}
              className="form-control"
              onChange={this.props.handleChangeGraph}
            >
              <option value="-1" disabled>
                Оберіть граф задач
              </option>
              {this.renderGraphList("tasks")}
            </select>
            {this.props.tasks !== -1 && (
              <GraphView
                manipulationActive={false}
                height="300px"
                name="tasks"
                data={this.props.tasksData}
                ref={this.tasksGraphRef}
              />
            )}
          </div>
          <div className="col">
            <label className="h6" htmlFor="cs">
              Граф задач:
            </label>
            <select
              value={this.props.cs}
              name="cs"
              id="cs"
              className="form-control"
              onChange={this.props.handleChangeGraph}
            >
              <option value="-1" disabled>
                Оберіть граф комп'ютерної системи
              </option>
              {this.renderGraphList("cs")}
            </select>
            {this.props.cs !== -1 && (
              <GraphView
                height="300px"
                manipulationActive={false}
                name="cs"
                data={this.props.csData}
                ref={this.csGraphRef}
              />
            )}
          </div>
        </div>
      </form>
    );
  }
}

export default ModellingForm;
