import React from "react";
import { Switch, Route } from "react-router-dom";
import {
  GraphEditorPage,
  GraphListPage,
  QueuePage,
  HomePage,
  ModellingPage
} from "../pages";
import ModellingView from "../components/ModellingView";
import { DataSet } from "vis";

const Routes = () => {
  return (
    <div className="container my-4">
      <Switch>
        <Route
          exact
          key="home-page"
          path="/"
          render={() => {
            document.title = `ПЗКС - 2`;
            return <HomePage />;
          }}
        />
        <Route
          key="tasks-graphs"
          exact
          path="/tasks"
          render={() => {
            document.title = `Графи задач | ПЗКС - 2`;
            return <GraphListPage name="tasks" title="Графи задач" />;
          }}
        />
        <Route
          exact
          path="/tasks/new"
          render={() => {
            document.title = `Новий граф задач | ПЗКС - 2`;
            return <GraphEditorPage name="tasks" />;
          }}
        />
        <Route
          path="/tasks/:id"
          exact
          type="task"
          render={props => {
            document.title = `Редактор графу задач | ПЗКС - 2`;
            return <GraphEditorPage name="tasks" id={props.match.params.id} />;
          }}
        />
        <Route
          path="/tasks/:id*/queue"
          exact
          type="task"
          render={props => {
            document.title = `Формування черги задач | ПЗКС - 2`;
            return <QueuePage id={props.match.params.id} />;
          }}
        />
        <Route
          key="cs-graphs"
          exact
          path="/cs"
          render={() => {
            document.title = `Графи комп'ютерних систем | ПЗКС - 2`;
            return (
              <GraphListPage name="cs" title="Графи комп'ютерної системи" />
            );
          }}
        />
        <Route
          exact
          path="/cs/new"
          render={() => {
            document.title = `Новий граф комп'ютерної системи | ПЗКС - 2`;
            return <GraphEditorPage name="cs" />;
          }}
        />
        <Route
          path="/cs/:id"
          type="task"
          render={props => {
            document.title = `Редактор графу комп'ютерної системи | ПЗКС - 2`;
            return <GraphEditorPage name="cs" id={props.match.params.id} />;
          }}
        />
        <Route
          path="/model"
          exact
          render={() => {
            document.title = `Моделювання | ПЗКС - 2`;
            return <ModellingPage />;
          }}
        />
        <Route
          path="/model/test"
          exact
          render={() => {
            document.title = `Моделювання (тестування) | ПЗКС - 2`;
            return (
              <ModellingView
                queue="1"
                assignmentAlgo="2"
                tasks={() => {
                  const tasks = JSON.parse(
                    window.localStorage.getItem("tasks")
                  );
                  let {
                    data: { nodes, edges }
                  } = tasks.filter(task => task.id === "_ewp4g4mn5")[0];
                  nodes = new DataSet(nodes);
                  edges = new DataSet(edges);
                  return { nodes, edges };
                }}
                cs={() => {
                  const cs = JSON.parse(window.localStorage.getItem("cs"));
                  let {
                    data: { nodes, edges }
                  } = cs.filter(cs => cs.id === "_mlog5xo8s")[0];
                  nodes = new DataSet(nodes);
                  edges = new DataSet(edges);
                  return { nodes, edges };
                }}
              />
            );
          }}
        />
      </Switch>
    </div>
  );
};

export default Routes;
